/* General Body Styles */
body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden; /* Prevent scrollbars */
}

/* App Container for Video and Content */
.app-container {
  position: relative;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Background Video */
.background-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1; /* Put the video behind the content */
}

/* App Content */
.app {
  padding: 30px 50px;
  text-align: center;
  max-width: 400px;
  width: 90%;
  background: rgba(255, 255, 255, 0.29);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5.8px);
  -webkit-backdrop-filter: blur(5.8px);
  border: 1px solid rgba(255, 255, 255, 0.4);
  z-index: 1; /* Ensure it's above the video */
}

h1 {
  margin-bottom: 25px;
  color: #333;
}

.search-form {
  display: flex;
  margin-bottom: 20px;
}

.search-input {
  flex: 1;
  padding: 10px 15px;
  border: 1px solid #ccc;
  border-radius: 4px 0 0 4px;
  outline: none;
  font-size: 1em;
}

.search-button {
  padding: 10px 20px;
  border: none;
  background-color: #4caf50;
  color: white;
  cursor: pointer;
  border-radius: 0 4px 4px 0;
  transition: background-color 0.3s ease;
  font-size: 1em;
}

.search-button:hover {
  background-color: #45a049;
}

.weather-container {
  margin-top: 20px;
}

.weather-info {
  display: flex;
  align-items: center;
  justify-content: center;
}

.weather-info img {
  width: 80px;
  height: 80px;
}

.weather-info p {
  font-size: 2em;
  margin: 0 0 0 10px;
  color: #333;
}

.description {
  text-transform: capitalize;
  margin: 10px 0;
  color: #555;
}

.error {
  color: red;
  margin-top: 10px;
  font-weight: bold;
}

.loading {
  color: #333;
  margin-top: 10px;
  font-style: italic;
}

/* Media Queries for Mobile Responsiveness */
@media (max-width: 500px) {
  .app {
    padding: 20px 30px;
  }

  .weather-info img {
    width: 60px;
    height: 60px;
  }

  .weather-info p {
    font-size: 1.5em;
  }
}

@media (max-width: 768px) {
  .app {
    padding: 25px 40px;
    max-width: 350px;
  }

  h1 {
    font-size: 1.8em;
  }

  .search-input {
    font-size: 0.9em;
  }

  .search-button {
    padding: 8px 15px;
    font-size: 0.9em;
  }

  .weather-info p {
    font-size: 1.8em;
  }
}
